* {
  margin: 0;
  padding: 0;
}

input {
  height: 25px;
  border: 0;
  border-radius: 10px;
  margin-right: 5px;
  padding-left: 5px;
}

button {
  height: 25px;
  width: 50px;
  color: #fff;
  background-color: gray;
  border: 1px;
  border-radius: 10px;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.box-form {
  width: 100%;
  background-color: #87ceeb;
  display: block;
}

.search-form {
  color: #3030e6;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin: 15px auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.gallery__photo-card {
  margin-bottom: 60px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info-item {
  text-align: center;
  color: #000;
  flex-direction: column;
  margin: auto;
  font-size: 12px;
  display: flex;
}

.gallery__photo-card:hover {
  transform: scale(1.03);
}

.photo-card__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.is-hidden {
  display: none;
}

.button_load {
  width: 100px;
  justify-content: center;
  align-items: center;
  margin: 20px auto auto;
  display: flex;
}

